.ufive_uptop {
  box-sizing: border-box;
  height: 50px;
  width: 50px;
  opacity: 0;
  cursor: pointer;
  z-index: 100;
  background-color: #f8d24e;
  border: none;
  border-radius: 50%;
  outline: none;
  margin-right: -30px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
  position: fixed;
  bottom: 50px;
  right: 50px;
}

.ufive_uptop:hover, .ufive_uptop:focus {
  background-color: #0a8817;
}

.ufive_uptop:hover {
  animation: pulse 1s linear infinite;
}

.ufive_upview {
  opacity: .6;
  margin-right: 0;
  transition: opacity .3s, margin-right .5s ease-out;
}

.ufive_uptop:before, .ufive_uptop:after {
  content: "";
  height: 18px;
  width: 2px;
  background-color: #84a178;
  position: absolute;
  top: 16px;
}

.ufive_uptop:before {
  left: 18px;
  transform: rotate(45deg);
}

.ufive_uptop:after {
  right: 18px;
  transform: rotate(-45deg);
}

@keyframes slide-to-bottom {
  0% {
    opacity: 0;
