  }
}

@keyframes slide-to-right {
  0% {
    opacity: 0;
    transform: translateX(-150px);
  }

  60% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes slide-to-left {
  0% {
    opacity: 0;
    transform: translateX(150px);
  }

  60% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    transform: translateX(0);
